<template>
  <div
    class="home"
    :class="{ min: screenWidth < 700, med: screenWidth < 1200 }"
  >
    <!--  -->
    <div class="header">
      <div class="title">
        <span class="name"></span>
        <p>
          <span class="describe"></span>
          <span class="webUrl" @click="copyUrL">https://subtoph.github.io</span>
        </p>
      </div>
    </div>
    <!--  -->
    <div class="contentBox">
      <!--  -->
      <div class="navBox">
        <!--  -->
        <div class="navBox_border" :class="{ clearPadding: !switchNavBox }">
          <!--  -->
          <my-transition>
            <MyNav
              v-show="switchNavBox"
              :actNav="actNav"
              :navList="navList"
              @changeNavAct="changeNavAct"
            ></MyNav>
          </my-transition>
          <!--  -->
          <div class="navBut" @click="switchNavBox = !switchNavBox">
            {{ switchNavBox ? "" : "" }}
          </div>
        </div>
      </div>
      <!--  -->
      <div class="content">
        <router-view />
      </div>
    </div>
  </div>
</template>
<script>
// 
import myTransition from "../utils/donghua.js";
// 
import MyNav from "../components/myNav.vue";
export default {
  name: "Home",
  components: {
    MyNav,
    "my-transition": myTransition,
  },
  data() {
    return {
      screenWidth: "", //
      switchNavBox: true, //
      actNav: "", //
      navList: [
        //
        {
          name: "",
          index: "notes",
        },
        {
          name: "",
          index: "about",
        },
      ],
    };
  },
  created() {},
  mounted() {
    this.monitorWidth(); //
  },

  methods: {
    // 
    monitorWidth() {
      //
      this.screenWidth = document.body.clientWidth;
      window.addEventListener("resize", () => {
        //
        this.screenWidth = document.body.clientWidth;
      });
    },
    // 
    changeNavAct(name, index) {
      this.actNav = name; //
      this.$router.push({ path: index }); //
    },
    //
    copyUrL(){
      // input
      const input = document.createElement('input')
      // body
      document.body.appendChild(input)
      // input
      input.setAttribute('value',"https://subtoph.github.io")
      // 
      input.select()
      // 
      // input
      // Input
      if (document.execCommand('copy')) {
        document.execCommand('copy')
      }
      alert('')   //
      // 
      document.body.removeChild(input)
    }
  },
};
</script>
<style lang="less" scoped>
.home {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgb(233, 233, 233);
  .header {
    position: fixed;
    top: 0;
    left: 0;
    height: 40px;
    background: #fff;
    width: 100%;
    z-index: 99;
    .title {
      width: 80%;
      line-height: 40px;
      text-align: left;
      margin: 0 auto;
      padding: 0 20px;
      box-sizing: border-box;
      overflow: hidden;
      span{
        display: block;
        float: left;
      }
      p{
        margin: 0;
        float: left;
        margin-left: 30px;
        span{
          float: none;
          line-height: 20px;
          color: #aaa;
        }
      }
      .name {
        font-weight: 700;
        font-size: 24px;
        font-style: italic;
      }
      
    }
  }
  

  .contentBox {
    position: absolute;
    background: rgb(40, 140, 255);
    height: 100%;
    width: 100%;
    padding: 0 200px;
    padding-top: 50px;
    padding-bottom: 10px;
    display: flex;
    box-sizing: border-box;
    transition: 0.3s;
    .navBox {
      margin-right: 10px;
      transition: 0.5s;
      width: 130px;
      .navBox_border {
        position: relative;
        border-radius: 5px;
        background: #fff;
        box-shadow: 0px 2px 2px rgb(117, 117, 117);
        padding: 10px 0;
        &.clearPadding {
          padding: 0;
        }
        .navBut {
          text-align: center;
          cursor: pointer;
          line-height: 40px;
          color: rgb(122, 122, 122);
          &:hover {
            color: rgb(0, 140, 255);
            background: rgb(213, 235, 255);
            border-radius: 5px;
          }
        }
      }
    }
    .content {
      position: relative;
      flex: 1;
      min-width: 700px;
      background: #fff;
      border-radius: 5px;
      padding: 10px;
    }
  }
}

// 
.home.min .header .name{
  font-size: 16px;
}
// 
.home.min .header .title{
  padding: 0 5px;
}
// 
.home.med .contentBox{
  padding: 0 10px;
  padding-top: 50px;
}
.home.med .contentBox .navBox{
  min-width: 80px;
  font-size: 12px;
}
.home.med .header .title{
  width: 90%;
}
.home.min .header .title{
  width: 100%;
}
 
</style>
