// 
const transitionStyle = '0.3s height ease-in-out'
const Transition = {
  //  eltransition,0
  beforeEnter (el) {
    // console.log(el.dataset.data)
    el.style.transition = transitionStyle
    el.style.height = 0
  },
  // 
  enter (el) {
    // 
    if (el.scrollHeight !== 0) {
      el.style.height = `${el.scrollHeight}px`
    } else {   //
      el.style.height = ''
    }
    el.style.overflow = 'hidden'
  },
  // 
  afterEnter (el) {
    // 
    // el.style.transition = ''
    el.style.height = ''
  },

  // 
  beforeLeave (el) {
    el.style.height = `${el.scrollHeight}px`
    el.style.overflow = 'hidden'
  },
  // 
  leave (el) {
    if (el.scrollHeight !== 0) {
      el.style.transition = transitionStyle
      el.style.height = 0
    }
  },
  // 
  afterLeave (el) {
    el.style.transition = ''
    el.style.height = ''
  }
}
export default {
  functional: true,
  render (h, {children}) { 
    return h('transition', { on: Transition}, children)
  }
}