import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
Vue.config.productionTip = false
// markdown
import 'github-markdown-css'
// devibeans   swh
import './assets/css/swh.css'

// md
import  { mdArticle } from './article/index'
Vue.use(mdArticle)
// 
import plugins from './components/index'
Vue.use(plugins);


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
